<template>
    <swiper :options="swiperOption" ref="swiper">
      <div class="mine-scroll-pull-down" v-if="pullDown">
        <lu-loading :text="pullDownText" inline ref="pullDownLoading" />
      </div>
      <swiper-slide>
        <slot></slot>
      </swiper-slide>
      <div class="mine-scroll-pull-up" v-if="pullUp">
        <lu-loading :text="pullUpText" inline ref="pullUpLoading"/>
      </div>
      <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
    </swiper>
</template>

<script type="text/ecmascript-6">
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import LuLoading from '@/base/loading/index.vue';
  import {
    PULL_DOWN_HEIGHT,
    PULL_DOWN_TEXT_INIT,
    PULL_DOWN_TEXT_START,
    PULL_DOWN_TEXT_ING,
    PULL_DOWN_TEXT_END,
    PULL_UP_HEIGHT,
    PULL_UP_TEXT_END,
    PULL_UP_TEXT_ING,
    PULL_UP_TEXT_INIT,
    PULL_UP_TEXT_START
  } from './config';
  export default {
    name: 'LuScroll',
    components: {
      swiper,
      swiperSlide,
      LuLoading
    },
    props: {
      scrollbar: {
        type: Boolean,
        default: true
      },
      data: {
        type: [Array, Object]
      },
      pullDown: {
        type: Boolean,
        default: false
      },
      pullUp: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      data() {
        this.update();
      }
    },
    created() {
      this.init();
    },
    methods: {
      // 初始化一部分不需要实时监听的数据
      init() {
        this.pulling = false; // 是否下拉的把标识符
        this.pullDownText = PULL_DOWN_TEXT_INIT;
        this.pullUpText = PULL_UP_TEXT_INIT;
        this.swiperOption = {// swiper插件的配置
          direction: 'vertical',
          slidesPerView: 'auto',
          freeMode: true,
          setWrapperSize: true,
          scrollbar: {
            el: this.scrollbar ? '.swiper-scrollbar' : null,
            hide: true
          },
          on: { // 监听滚动条
            sliderMove: this.scroll,
            touchEnd: this.touchEnd,
            transitionEnd: this.scrollEnd // 当松开手指时会继续随着惯性滑动一段距离，监听滑动完成后的状态
          }
        };
      },
      // 数据更新
      update() {
        this.$refs.swiper && this.$refs.swiper.swiper.update();
      },
      // 返回顶部
      scrollToTop(speed, runCallbacks) {
        this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0, speed, runCallbacks);
        this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper);
      },
      // 滚动条发生变化执行的函数
      scroll() {
        const swiper = this.$refs.swiper.swiper;
        this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper);
        if (this.pulling) {
          return;
        }
        if (swiper.translate > 0) { // 下拉
          if (!this.pullDown) {
            return;
          }
          if (swiper.translate > PULL_DOWN_HEIGHT) {
            // 更换文字
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
          } else {
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);
          }
        } else if (swiper.isEnd) { //  上拉
          if (!this.pullUp) {
            return;
          }
          // 判断是否达到上拉触发条件
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > parseInt(swiper.$wrapperEl.css('height'));
          if (isPullUp) {
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
          } else {
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
          }
        }
      },
      scrollEnd() {
        this.$emit('scroll-end', this.$refs.swiper.swiper.translate, this.$refs.swiper.swiper, this.pulling);
      },
      // 上下拉松开后执行的函数
      touchEnd() {
        const swiper = this.$refs.swiper.swiper;
        if (this.pulling) {
          return;
        }
        if (swiper.translate > 0) {
          if (!this.pullDown) {
            return;
          }
          this.pulling = true;
          if (swiper.translate > PULL_DOWN_HEIGHT) {
            // 禁止触摸
            swiper.allowTouchMove = false;
            swiper.setTransition(swiper.params.speed);
            // 回到80的高度
            swiper.setTranslate(PULL_DOWN_HEIGHT);
            // 定住不给回弹
            swiper.params.virtualTranslate = true;
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING);
          }
          // 触发一个事件
          this.$emit('pull-down', this.pullDownEnd);
        } else if (swiper.isEnd) { // 是否拉到了底部
          const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > totalHeight;
          if (isPullUp) {
            if (!this.pullUp) {
              return;
            }
            this.pulling = true;
            // 禁止触摸
            swiper.allowTouchMove = false;
            swiper.setTransition(swiper.params.speed);
            swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height));
            // 定住不给回弹
            swiper.params.virtualTranslate = true;
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING);
            this.$emit('pull-up', this.pullUpEnd);// 触发一个事件
          }
        }
      },
      // 下拉完成后的一些还原
      pullDownEnd() {
        const swiper = this.$refs.swiper.swiper;
        this.pulling = false;
        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
        swiper.setTransition(swiper.params.speed);
        swiper.setTranslate(0);
        // 下拉完成后一定时间内触发函数（此处具体为header部分的出现和消失）
        setTimeout(() => {
          this.$emit('pull-down-transition-end');
        }, swiper.params.speed);
      },
      // 上拉完成后的一部分数据还原
      pullUpEnd() {
        const swiper = this.$refs.swiper.swiper;
        this.pulling = false;
        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
      }
    }
  };
</script>
<style lang="scss" scoped>
.swiper-container{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
  .swiper-slide{
    height: auto;
  }
  .mine-scroll-pull-down{
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 80px;
    width: 100%;
  }
  .mine-scroll-pull-up {
      position: absolute;
      left: 0;
      width: 100%;
      height: 30px;
      top: 100%;
   }
</style>



