<template>
    <div class="mine-search-box-wrapper">
      <i class="iconfont icon-search"></i>
      <div class="mine-search-box" v-if="fake">{{placeholder}}</div>
      <input
        type="text"
        class="mine-search-box"
        title="搜索框"
        :placeholder="placeholder"
        ref="input"
        v-model="query"
        v-if="!fake"
      >
      <i
        class="iconfont icon-close"
        v-show="query"
        @click="reset"
      ></i>
    </div>
</template>

<script>
  import {debounce} from '@/assets/js/util.js';
  export default {
    name: 'LuSearchBox',
    props: {
      placeholder: {
        type: String,
        default: ''
      },
      fake: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        query: ''
      };
    },
    watch: {
      query: debounce(function () {
        this.$emit('query', this.query);
      })
    },
    methods: {
      // 获取焦点
      focus() {
        this.$refs.input && this.$refs.input.focus();
      },
      // 清除input中的内容
      clear() {
        this.query = '';
      },
      // 重置
      reset() {
        this.focus();
        this.clear();
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";

  $search-box-height: 30px;

  .mine-search-box-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height: $search-box-height;
    padding: 0 7px;
    background-color: #fff;
    border-radius: $search-box-height / 2;
  }

  .iconfont {
    color: $icon-color;
    font-size: $icon-font-size-sm;
    font-weight: bold;
  }

  .mine-search-box {
    flex: 1;
    background: none;
    border: none;
    margin: 0 6px;
    color: #666;
    line-height: 1.5;
  }
</style>



