// 简易的节流函数
export const debounce = (callbackFunction, delay = 200) => {
  let timer = null;
  return function (...args) {
    timer && clearTimeout(timer);
    timer = setTimeout(() => {
      callbackFunction.apply(this, args);
    }, delay);
  };
};



