<template>
    <div class="mine-navbar">
      <div class="mine-navbar-left">
        <slot name="left"></slot>
      </div>
      <div class="mine-navbar-center" v-if="$slots.center">
        <slot name="center"></slot>
      </div>
      <div class="mine-navbar-right" v-if="$slots.right">
        <slot name="right"></slot>
      </div>
      <h1 class="mine-navbar-title" v-if="title">
        <span class="mine-navbar-text"  v-text="title"></span>
      </h1>
    </div>
</template>

<script>
  export default {
    name: 'LuNavbar',
    props: {
      title: {
        type: String,
        default: ''
      }
    }
  };
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/mixins';
.mine-navbar{
  position: relative;
  @include flex-between();
  height: 50px;
  background-color: #fff;
  &-left{
    margin-left: 10px;
  }
  &-center{
    margin: 0 10px;
    flex: 1;
  }
  &-right{
    margin-right: 10px;
  }
  &-title{
    position: absolute;
    top: 0;
    bottom: 0;
    @include flex-center();
    text-align: center;
    left: 20%;
    right: 20%;
  }
  &-text{
    @include ellipsis();
    font-size: 18px;
    line-height: 1.5;
    width: 100%;
  }
}
</style>



